import { axiosClient } from "@rp/react/axios-client"
import { CONTENT_SOURCES_ROUTE } from "../content.constants"
import { ContentSource } from "../content.types"

type GetContentSourceByID = {
  id: string
}

async function getContentSourceByID({ id }: GetContentSourceByID): Promise<ContentSource> {
  const { data } = await axiosClient.get<ContentSource>(`${CONTENT_SOURCES_ROUTE}/${id}`)

  return data
}

export { getContentSourceByID }
